const Component2 = ({ name }) => {
  const {
    counters, addCounter, incCounter, decCounter,
  } = useContext(GlobalContext);
  const { time, value: counter } = counters[name] || {};

  const handleIncrease = useCallback(() => {
    incCounter(name);
  }, [incCounter, name]);

  const handleDecrease = useCallback(() => {
    decCounter(name);
  }, [decCounter, name]);

  useEffect(() => {
    addCounter(name);
  }, [addCounter, name]);

  return (
    <div className="columns is-multiline">
      <div className="column is-12">
        <h4 className="subtitle is-6">{name}: {counter} (atualizado em {time})</h4>
        <p>
          Ao clicar nos botes em azul pode ser gerado um novo render deste Componente Pai
          assim como seu coponentes filhos que no esto memoizados da forma correta, mesmo
          que eles no usem o valor do contador do Componente Pai e no teriam a
          necessidade de sofrer um novo render
        </p>
        <button className="button is-link is-light mr-4" type="button" onClick={handleIncrease}>+</button>
        <button className="button is-link is-light mr-4" type="button" onClick={handleDecrease}>-</button>
      </div>
      <div className="column is-12">
        <p>
          Ao clicar nos botes abaixo pode conferir sua independncia, no causando render um
          ao outro ou no Componente Pai. Porm ao clicar nos botes em azul pdoe conferir que
          o boto memoizado no sofre um render desncessrio enquanto o outro renderzida novamente.
        </p>
        <Child text={`Boto 1 - ${name}`} />
        <ChildMemo text={`Boto 1 com Memo - ${name}`} />
      </div>
      <div className="column is-12">
        <p>
          Os botes abaixo atualizam o contador do Componente Pai e assim causam um novo render
          nele e em todos seus componentes filhos sem memoizao. Este exemplo torna evidente
          como o no uso ou mal uso da memoizao podem causar renders inesperados.
        </p>
        <Child text={`Boto 2 - ${name}`} onClick={handleIncrease} />
        <ChildMemo text={`Boto 2 com Memo - ${name}`} onClick={handleIncrease} />
      </div>
      <div className="column is-12">
        <p>
          Neste terceiro cenario nosso boto memoizado esta sofrendo o render sempre que o
          componente pai renderiza e isso ocorre pq apesar de memoizado sua prop onClick 
          uma funo no memoizada com o useCallback e isso faz com que ela seja recriada
          a cada render causando um novo render no boto memoizado que entende que houveram
          atualizaes.
        </p>
        <Child text={`Boto 3 - ${name}`} onClick={handleDecrease} />
        <ChildMemo text={`Boto 3 com Memo - ${name}`} onClick={handleDecrease} />
      </div>
    </div>
  );
};

